a {
  text-decoration: none;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  min-width: 320px;
  margin: 0 auto;
  font-family: "Opensans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 30px;
  font-weight: 400;
  color: black;
  letter-spacing: 0;
}


.container {
  width: 100%;
}

.overlay {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.85);
  z-index: 10;

  &--show {
    display: block;
  }
}
